@import "../../../style/theme/default/var.css";
@component-namespace userinfo{
  @b content{
    border-left: var(--border-width-base) var(--border-style-base) var(--theme-border-color-primary);
    position: relative;
    min-height: 1px;
  /*  padding-left: var(--theme-padding-middle);*/
  }

  @b content-popover[x-placement^=bottom]{
    background-color: var(--theme-color-primary);
    margin-top: var(--theme-margin-small);
    -webkit-border-radius: var(--border-radius-base);
    -moz-border-radius: var(--border-radius-base);
    border-radius: var(--border-radius-base);
    .popper__arrow:after{
      border-bottom-color: var(--theme-color-primary);
    }
    .el-checkbox{;
      display: block;
      margin-left: 0;
      color: var(--color-white);
    }
    .el-checkbox .el-checkbox__inner{
      background-color: var(--theme-color-primary);
      border-color: var(--color-white);
    }
    .el-checkbox__input.is-checked .el-checkbox__inner:after {
      border-color:var(--color-white);
    }
  }
  @b button{
    background-color: var(--theme-background-color-secondary);
    border: none;
  }
  @b button-select{
    background-color: var(--theme-color-primary);
    color: var(--color-white);
  }
}
.userinfo .el-dialog__body{
  background: var(--color-white)!important;
}
.userinfo .el-tabs.el-tabs--card > .el-tabs__content{
  padding-bottom: 0;
}
.userinfo .el-tabs.el-tabs--card > .el-tabs__header .el-tabs__item{
  padding:0 var(--theme-padding-middle);

}
/*.userinfo .el-tabs.is-stick{
  position: relative;
  .el-tabs__header{
    position: sticky;
    position: -webkit-sticky;
    top:0;
    z-index: 2;
  }
  .el-tabs__content{
    -webkit-margin-top:35px;
    -moz-margin-top:35px;
    -o-margin-top:35px;
    -ms-margin-top:0;
  }
}*/
.userinfo-collapse.el-collapse{
  border: none;
}
.userinfo-collapse.el-collapse .el-collapse-item__wrap{
  border-bottom:0;
}
.userinfo-collapse.el-collapse .el-collapse-item__content{
  background: var(--color-white);
  padding: var(--theme-padding-small) 0;
}
.userinfo-collapse.el-collapse .el-collapse-item__header{
  margin-top: 0;
  margin-bottom: 0;
  background-color: var(--theme-background-color-primary);
}
.userinfo-button:hover,.userinfo-button:focus{
  background-color: var(--theme-color-primary);
}

@component-namespace contrail{
  @b content{
    margin-bottom: var(--theme-margin-small);
  }
  @b card{
    @e photo{
      display: table-cell;
      padding-right:var(--theme-padding-base);
      vertical-align: top;
    }
    @e property{
      display: table-cell;
      .contrail-content{
        &:last-child{
      margin-bottom:0;
         }
      }
      .contrail-inline{
        margin-right: var(--theme-margin-middle);
      }
    }
  }
}

@component-namespace tmp{
  @b userinfo{
    @e scrollbar{
      height:328px;
    }
    @e sidebar{
    }
    @e header{
      border-bottom: 1px solid var(--theme-border-color-primary);
      margin-bottom: var(--theme-margin-small);
      & > p{
      margin-bottom: var(--theme-margin-small);
          }
      & .avatar{
        width:30px;
        height:30px;
        border-radius: 50%;
      }
      & .name{
        font-size: 18px;
        margin-left: var(--theme-margin-base);
        display: inline-block;
        vertical-align: middle;
        color: var(--theme-font-color-primary);
      }
    }
    @e table {
      border: 0;
      &:after,
      &:before{
        background-color: transparent;
        height:0;
       }
      &.el-table th {
        background-color: transparent;
      }
      &.el-table th.is-leaf,
       &.el-table td{
        border-bottom: 0px;
       }
      .el-table__header-wrapper thead div {
        background-color: transparent;
        color: var(--theme-font-color-primary);
        font-size: var(--theme-font-size-small);
        font-weight: bold;
      }
    }

    @e form{
      .el-form-item {
        margin-bottom: 0;
      }
    }
  }
}

@component-namespace bus{
  @b form{
    @e edit{
      position: relative;
      display: inline-block;
      padding-right: 30px;
      @when status {
        padding-right: var(--theme-padding-middle);
      }
      &:hover{
        .bus-form__edit-icon.is-noedit > i {
          display: inline-block;
        }
      }
      .el-form-grid{
        margin-right:0;
      }
      .el-form-grid .el-radio,
      .el-form-grid .el-radio-group .el-radio{
        margin-right: 5px;
      }
    }
    @e edit-control{
      position: absolute;
      top:50%;
      right:-5px;
      transform: translate(0,-50%);
    }
    @e edit-icon {
      display: inline-block;
      cursor: pointer;
       @when noedit {
         & > i {
           display: none;
         }
       }
    }
  }
}

@component-namespace goodsinfo{
  @b property{
    width:100%;
    display:table;
    margin-bottom:var(--theme-margin-small);
    @e photo{
      width:120px;
      padding-right: var(--theme-padding-base);
    }
    .control-form  .control-item  .control-label{
      width:120px;
    }
    .control-form.tmp-table-cell h6{ margin-top: 0;}
  }
  @b form{
    margin-bottom:var(--theme-margin-base);
  }
}

.cbp_tmtimeline {
  margin:0;
  padding: 0;
  list-style: none;
  position: relative;
}

/* The line */
.cbp_tmtimeline:before {
  content: '';
  position: absolute;
  top: 5px;
  bottom: 0;
  width: 1px;
  background: var(--theme-border-color-primary);
  left: 115px;
}

/* The date/time */
.cbp_tmtimeline > li {
  position: relative;
}

.cbp_tmtimeline > li .cbp_tmtime {
  display: block;
  width: 90px;
  margin-top: 4px;
  position: absolute;
  top: 50%;
  left:0;
  transform: translate(0,-50%);
  -webkit-transform: translate(0,-50%);
}

.cbp_tmtimeline > li .cbp_tmtime span {
  display: block;
  text-align: right;
}

.cbp_tmtimeline > li .cbp_tmtime span:first-child {
  font-size: 13px;
  color:var(--theme-font-color-primary);
}

.cbp_tmtimeline > li .cbp_tmtime span:last-child {
  font-size: var(--theme-font-size-small);
  color: var(--theme-font-color-secondary);
}


/* Right content */
.cbp_tmtimeline > li .cbp_tmlabel {
  margin:5px 0 5px 150px;
  background: var(--color-white);
  border:1px solid var(--theme-border-color-primary);
  padding: 5px 10px;
  font-size: 12px;
  font-weight: normal;
  line-height: 1.4;
  position: relative;
  border-radius: 5px;
  &.is-empty {
    display: inline-block;
    & .contrail-content{
      margin-bottom: 0;
      font-size: 14px;
    }
   }
}
/* The triangle */
.cbp_tmtimeline > li .cbp_tmlabel:after {
  left: -6px;
  content: " ";
  position: absolute;
  top: 50%;
  margin-top: -4px;
  width: 8px;
  height: 8px;
  border-top: 1px solid var(--theme-border-color-primary);
  border-right: 1px solid var(--theme-border-color-primary);
  background-color: var(--color-white);
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}
/* The icons */
.cbp_tmtimeline > li .cbp_tmicon {
  width: 30px;
  height: 30px;
  speak: none;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
  font-size: var(--theme-font-size-small);
  line-height: 30px;
  -webkit-font-smoothing: antialiased;
  position: absolute;
  color: var(--color-white);
  background: var(--theme-font-color-primary);
  border-radius: 50%;
  text-align: center;
  left: 120px;
  top:50%;
  margin:-15px 0  0 -20px;
  &.timeline-bg-primary{
     background: var(--theme-color-primary);
   }
}




